<template>
    <div>
       <el-breadcrumb >
        <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item>商品管理</el-breadcrumb-item>
        <el-breadcrumb-item>商品分类</el-breadcrumb-item>
        </el-breadcrumb>
          <el-card class="box-card">
            <el-row>
                <el-col>
                    <el-button type="primary" @click="tjfl">添加分类</el-button>
                </el-col>
            </el-row>
            <tree-table :data="cateList" :columns="columns" :selection-type="false" :expand-type="false" show-index border>
                <template slot="isok" slot-scope="scope">
                    <i class="el-icon-success" v-if="scope.row.cat_deleted === false" style="color:lightgreen"></i>
                    <i class="el-icon-error" v-else style="color:red"></i>
                </template>
                <template slot="order" slot-scope="scope">
                    <el-tag size="mini" v-if="scope.row.cat_level =='0' ">一级</el-tag>
                    <el-tag  size="mini" v-else-if="scope.row.cat_level =='1'" type="success" >二级</el-tag>
                    <el-tag  size="mini" type="warning" v-else>三级</el-tag>
                </template>
                  <template slot="opt" slot-scope="scope">
                   <el-button type="primary" icon="el-icon-edit"></el-button>
                    <el-button type="primary" icon="el-icon-delete"></el-button>
                </template>
            </tree-table>
              <el-dialog
                title="添加分类"
                :visible.sync="tjflDialog"
                @close="fpjsClose"
                width="50%">
                 <el-form :model="CateaddForm" :rules="CateadRules" ref="CateaFormRef" label-width="120px">
                    <el-form-item label="分类名称" prop="cat_name" >
                        <el-input v-model="CateaddForm.cat_name" />
                    </el-form-item>
                      <el-form-item label="父级分类" >
                           <el-cascader
                            v-model="value"
                            :options="optionsa"
                            expand-trigger="hover"
                            :props="CateProps"
                            clearable
                            @change="handleChange" change-on-selects="true"></el-cascader>
                    </el-form-item>
                </el-form>
                <span slot="footer" class="dialog-footer">
                    <el-button @click="tjflDialog = false">取 消</el-button>
                    <el-button type="primary" @click="tjflbtn()"> 确 定</el-button>
                </span>
              </el-dialog>
          </el-card>
    </div>
</template>
<script>
export default {
    data() {
        return {
            value:[],
            CateProps:{
                 value:'cat_id',
                 label:'cat_name',
                 children:'h'
            },
            optionsa:[],
            CateadRules:{
                 cat_name:[
                     {required:true,message:'请输入名称',trigger:'blur'},
                 ],
            },
            CateaddForm:{
                cat_name:'',
                cat_id:0,
                cat_level:0,
            },
            tjflDialog:false,
            queryInfo:{
                type:3,
                pagenum:2,
                pagesize:10
            },
            total:0,
            cateList:[],
            columns:[
                {
                    label:'分类名称',
                    prop:'cat_name'
                },
                {
                    label:'是否有效',
                    type:"template",
                    template:'isok'
                },
                {
                    label:'排序',
                    type:"template",
                    template:'order'
                },
                 {
                    label:'操作',
                    type:"template",
                    template:'opt'
                },
            ]
        };
    },
    created(){
        this.getCateList()
    },
    mounted() {
        
    },

    methods: {
     async getCateList(){
         const {data:res} = await this.$http.get('categories',{parmas:this.queryInfo});
          if(res.meta.status != 200) return this.$message.error('获取商品分类失败')
         this.cateList = res.data;
         this.total = res.data.total;
        },
        handleSizeChange(newSize){
         this.queryInfo.pagesize = newSize;
         this.getCateList()
         },
       handleCurrentChange(newPage){
         this.queryInfo.pagenum = newPage;
         this.getCateList()
      },
      tjfl(){
          this.getParentCateList();
          this.tjflDialog = true;
      },
      fpjsClose(){
          this.$refs.CateaFormRef.resetFields()
          this.value = []; 
          this.CateaddForm.cat_id = 0;
          this.CateaddForm.cat_level = 0;
      },
      tjflbtn(){
        console.log(this.CateaddForm)
        this.$refs.CateaFormRef.validate(async valid=>{
            if(!valid)return
            const {data:res} = await this.$http.post('categories',this.CateaddForm);
            if(res.meta.status !==201) return this.$message.error('添加分类失败')
             this.$message.success('添加分类成功')
             this.getCateList()
        })
      },
      async getParentCateList(){
            const {data:res} = await this.$http.get('categories',{parmas:{type:2}});
             if(res.meta.status != 200) return this.$message.error('获取商品分类失败')
                this.optionsa = res.data;
             this.optionsa.forEach(element => {
                    element.h = element.children;
             })
             console.log(this.optionsa)
      },
      handleChange(){
          if(this.value.length > 0){
            this.CateaddForm.cat_id = this.value[this.value.length - 1];
            this.CateaddForm.cat_level = this.value.length;
            return
          }else{
              this.CateaddForm.cat_id = 0;
               this.CateaddForm.cat_level = 0;
          }
      }
    },
};
</script>
<style lang="less" scoped>
    .el-breadcrumb{
        margin-bottom: 20px;
    }
</style>